<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<!-- <el-form-item>

                    <el-alert title="" type="info" :closable="false" > </el-alert>

					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item> -->
                <el-tooltip class="item" effect="dark" content="" placement="right">
                     <el-button type="primary" @click="handleAdd"></el-button>
                </el-tooltip>

				

			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>

			<el-table-column type="index" label="ID" width="60">
			</el-table-column>

			<el-table-column prop="name" label="" width="100" sortable>
			</el-table-column> 
			
			<el-table-column prop="nick" label="" min-width="100" sortable>
			</el-table-column>

			<el-table-column prop="sex" label="" width="100" :formatter="formatSex" sortable>
			</el-table-column>
			
			<el-table-column prop="phone" label="" min-width="150" sortable>
			</el-table-column>

			<el-table-column prop="email" label="" min-width="150" sortable>
			</el-table-column>

			<el-table-column prop="departid" label="" min-width="100" :formatter="formatName" sortable>
			</el-table-column>
			
			<el-table-column label="" width="200">
				<template scope="scope">

					<el-button size="small" @click="EditPassword(scope.$index, scope.row)"></el-button>

				 	<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
				  
					<!-- <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button> -->
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" ></el-input>
				</el-form-item>

				<el-form-item label="" prop="nick">
					<el-input v-model="editForm.nick" ></el-input>
				</el-form-item>

              <!--   <el-form-item label="" prop="password">
					<el-input type="password" v-model="editForm.password" ></el-input>
				</el-form-item> -->

				<el-form-item label="">
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="2"></el-radio>
					</el-radio-group>
				</el-form-item>

				<el-form-item label="">
					<!-- <el-input v-model="editForm.departid"></el-input> -->
					<el-select v-model="editForm.departid" placeholder="">
					 <el-option v-for="departList in departList" :label="departList.name" :value="departList.id" ></el-option>  
					</el-select>
				</el-form-item>

				<el-form-item label="">
					<el-input  v-model="editForm.email"></el-input>
				</el-form-item>

				<el-form-item label="">
					<el-input v-model="editForm.phone"></el-input>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>

				<el-form-item label="">
					<el-input v-model="addForm.nick" auto-complete="off"></el-input>
				</el-form-item>

				<el-form-item  label="">
					<el-input type="password" v-model="addForm.password" auto-complete="off"></el-input>
				</el-form-item>

				<el-form-item label=""> 
					<el-radio-group v-model="addForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="2"></el-radio>
					</el-radio-group> 
				</el-form-item>
				
				<el-form-item label="">
					<!-- <el-input v-model="addForm.departid"></el-input> -->
					<el-select v-model="addForm.departid" placeholder="">
					 <el-option v-for="departList in departList" :label="departList.name" :value="departList.id" ></el-option>   
					</el-select>
				</el-form-item>


				
				<el-form-item label="">
					<el-input  v-model="addForm.email"></el-input>
				</el-form-item>

				<el-form-item label="">
					<el-input  v-model="addForm.phone"></el-input>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>


        <!---->
		<el-dialog title="" v-model="editPasswordVisible" :close-on-click-modal="false">
			<el-form :model="editPassword" label-width="150px" :rules="editPasswordRules" ref="editPassword">



                <el-form-item  label="">
					<el-input type="text" :disabled="true" v-model="editPassword.name" auto-complete="off"></el-input>
					
				</el-form-item>

				<el-form-item  label="">
					<el-input type="password" v-model="editPassword.password" auto-complete="off"></el-input>
					
				</el-form-item>

				<el-form-item  label="">
					<el-input type="password" v-model="editPassword.passwordagain" auto-complete="off"></el-input>
					
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editPasswordVisible = false"></el-button>
				<el-button type="primary" @click.native="decidePassword" :loading="editPasswordLoading"></el-button>
			</div>
		</el-dialog> 

	</section>
</template>

<script >
	import util from '../../common/js/util'
    //import md5 from 'js-md5';
	//import NProgress from 'nprogress'
	import {updatePasswordRequest,listDepartRequest,listUsersRequest, removeUserRequest, addUserRequest,updateUserRequest } from '../../api/api';
 
  
	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,

          
				departs :[],
				num : 0,
                departList  : [],

				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					name: '',
					nick:'',
					//password:'',
					sex: 0,
				    departid:null,
				    phone:'',
					email: ''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					password: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					name: '',
					nick:'',
					password:'',
					sex: 0,
				    departid: null,
				    phone:'',
					email: ''
				},

				editPasswordVisible: false,//
				editPasswordLoading: false,//
				editPasswordRules:{        //
					
					password: [
						{ required: true, message: '', trigger: 'blur' }
					],
					passwordagain: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				editPassword : {  //
					id:'',
					name:'',
					password:'',
					passwordagain:''
				}

			}
		},
		methods: {
			decidePassword (){
              
                   if(this.editPassword.password!=this.editPassword.passwordagain)
                   {
                     this.$message({
									message: '',
									type: 'warning'
								});
                   }
                   else{
                   	this. passwordSubmit();
                   }
                   
			},
            formatName :function(row){
                     
                 for(var j=0 ; j<this.departList.length ; j++){
                     var cp =
                     {  
                     	id:0,
                     	name:""
                     }
                     cp=this.departList[j];
                     if(row.departid==cp.id){
                     	return cp.name;
                     }
                     else{}
                 }
                 return "";
			},
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 2 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				

                listDepartRequest(para).then((res)=>{
                        this.departs =JSON.parse(res.data).depart;
                        this.num = this.departs.length;
                        this.departList = [];
                    for(var key in this.departs){
                        this.departList.push({id:this.departs[key].id, name:this.departs[key].departname});
                    }
                });

				listUsersRequest(para).then(data => {
                    console.log(data);
					if(data.code==1){
						this.users=[];
						this.total=0;
						this.listLoading = false;
					}
					else if (data.code==0)
					{  
                       this.users = JSON.parse(data.data).user;
					   this.total = this.users.length; 
					   this.listLoading = false;
					}
					else{
						this.listLoading=false;
						this.$message({
							message:'',
							type:'warning'
						});
					} 
					
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
                 
					let para =[];
					para.push({id:row.id});   

					removeUserRequest(para).then(data => {
						this.listLoading = false;
						//NProgress.done();
						if(data.code==0){
                                this.$message({
								message: '',
								type: 'success'
							});
							}
							else if(data.code==1){
								this.$message({
								message: '',
								type: 'warning'
							});
							}
							else {
	         						this.$message({
									message: '',
									type: 'error'
								});
							}
    					this.getUsers();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;

				this.editForm = Object.assign({}, row);
				/*this.editForm ={
                    name: '',
					password:'',
					sex: -1,
					age: 0,
					birth: '',
					email: ''
				};*/
			},
			//
			EditPassword: function (index, row) {
				this.editPasswordVisible = true;
                
				//this.editPassword = Object.assign({}, row);
				this.editPassword={
					name:'',
					password:'',
					passwordagain:''
				};
				//console.log(row.name);
				this.editPassword.name=row.name;
				this.editPassword.id=row.id;
				

			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					nick:'',
					password:'',
					sex: 0,
				    departid:null,
				    phone:'',
					email: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							//this.editForm.password=this.md5(this.editForm.password);
							let para = Object.assign({}, this.editForm);
							//para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							updateUserRequest(para).then(data => {
								this.editLoading = false;
								//NProgress.done();
								if(data.code==0){
                                   this.$message({
									message: '',
									type: 'success'
								});
								}
								else if(data.code==1){
									this.$message({
									message: '',
									type: 'warning'
								});
								}
								else {
									this.$message({
										message: '',
										type: 'error'
									});
								}
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							this.addForm.password=this.md5(this.addForm.password);
							let para = Object.assign({}, this.addForm);
							//para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUserRequest(para).then(data => {
                                
								this.addLoading = false;
								if(data.code==0){
                                   this.$message({
									message: '',
									type: 'success'
								});
								}
								else if(data.code==1){
									this.$message({
									message: '',
									type: 'warning'
								});
								}
								else {
									this.$message({
										message: '',
										type: 'error'
									});
								}
								
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//
			passwordSubmit: function () {
				this.$refs.editPassword.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editPasswordLoading = true;
							
						  
							this.editPassword.password=this.md5(this.editPassword.password);  
                            //console.log(this.editPassword.id);
					        
					        let para ={id:this.editPassword.id,password:this.editPassword.password};
			   
						    //console.log(":"+para);
							updatePasswordRequest(para).then(data => {

								this.editPasswordLoading = false;
								//NProgress.done();
								if(data.code==0){
                                   this.$message({
									message: '',
									type: 'success'
								});
								}
								else if(data.code==1){
									this.$message({
									message: '',
									type: 'warning'
								});
								}
								else {
									this.$message({
										message: '',
										type: 'error'
									});
								}
								this.$refs['editPassword'].resetFields();
								this.editPasswordVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
                //console.log(this.sels.map(item => item.id));
				//var ids = this.sels.map(item => item.id).toString();
				
    			let idss=[ ];
                let sss =this.sels.map(item => item.id);
				for(var key in sss)  
				{
                     idss.push({id:sss[key]})  ;  
                };

                //console.log(idss);

			
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					removeUserRequest(idss).then(data => {
						this.listLoading = false;
						//NProgress.done();
						if(data.code==0){
                                this.$message({
								message: '',
								type: 'success'
							});
							}
							else if(data.code==1){
								this.$message({
								message: '',
								type: 'warning'
							});
							}
							else {
								this.$message({
								message: '',
								type: 'error'
								});
								}
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>


<style scoped>

</style>


